import style from "./UnconfirmedListDesktop.module.scss"
import {IUnconfirmedToken} from "../data_unconfirmedToken";
import {FC} from "react";
import {useStore} from "../../../../store/rootStore";
import {observer} from "mobx-react-lite";
import {UnconfirmedRow} from "../UnconfirmedRow/UnconfirmedRow";

export const UnconfirmedListDesktop: FC<{tokens: IUnconfirmedToken[]}> = observer(({
                                                                              tokens}
) => {
    const {
        appStore: {
            colorTheme
        }
    } = useStore();

    return (
        <div className={style.unconfirmedListDesktop}>

            <div className={style.listHeader}>
                {
                    [
                        "# Listed",
                        "Followers",
                        "Pledge Amount",
                        "Statistics",
                        "Share",
                        "Pledge"
                    ].map((label, key) => (
                        <p key={key}>{label}</p>
                    ))
                }
            </div>

            <div className={style.listBody}>
                {
                    tokens.map((data, key) => (
                        <UnconfirmedRow key={key}
                                        {...data}
                        />
                    ))
                }
            </div>
        </div>
    )
})