import style from "./ConfirmedListDesktop.module.scss";
import {FC} from "react";
import {observer} from "mobx-react-lite";
import {useStore} from "../../../../store/rootStore";
import {IConfirmedToken} from "../data_confirmedToken";
import {ConfirmedRow} from "../ConfirmedRow/ConfirmedRow";

export const ConfirmedListDesktop: FC<{ tokens: IConfirmedToken[] }> = observer(({
                                                                                       tokens
                                                                                   }) => {
    const {
        appStore: {
            colorTheme
        }
    } = useStore();

    return (
        <div className={style.confirmedListDesktop}>

            <div className={style.listHeader}>
                {
                    [
                        "# Listed",
                        "Created",
                        "Followers",
                        "Marketcap (24h)",
                        "Current price",
                        "Statistics",
                        "Share",
                        "Buy"
                    ].map((label, key) => (
                        <p key={key}>{label}</p>
                    ))
                }
            </div>

            <div className={style.listBody}>
                {
                    tokens.map((data, key) => (
                        <ConfirmedRow key={key}
                                        {...data}
                        />
                    ))
                }
            </div>

        </div>
    )
})