import {ITrade} from "../data";
import {FC} from "react";
import style from "./TableDesktop.module.scss";
import {clsx} from "clsx";
import {svgIcons} from "../../../../assets/svgIcons";

export const TableDesktop: FC<{ trades: ITrade[] }> = ({trades}) => {
    return (
        <div className={style.tableDesktop}>
            <div className={style.header}>
                {
                    [
                        "Holders (5)",
                        "Type",
                        "Pledge amount",
                        "Time",
                        "Transaction"
                    ].map((label, key) => (
                        <p key={key}>
                            {label}
                        </p>
                    ))
                }
            </div>
            <div className={style.body}>
                {
                    trades.map(({
                                    src,
                                    name,
                                    type,
                                    pledgeAmount,
                                    time,
                                    transaction,
                                }, key) => (
                        <div key={key}
                             className={style.row}
                        >

                            <div className={style.holders}>
                                <img src={src} alt=""/>
                                <p>{name}</p>
                            </div>

                            <div className={clsx({
                                [style.type]: true,
                                [style.type_sell]: type === "Pledged",
                            })}>
                                <p>
                                    {type === "Pledged" ? "Pledged" : "Unpledged"}
                                </p>
                            </div>

                            <div className={style.price}>
                                {svgIcons.solana}
                                <p>
                                    {pledgeAmount[0]}<span>/${pledgeAmount[1]}</span>
                                </p>
                            </div>


                            <div className={style.time}>
                                {
                                    [
                                        ...time[0].split(""),
                                        time[1]
                                    ].map((string, key) => (
                                        <p key={key}>
                                            {string}
                                        </p>
                                    ))
                                }
                            </div>

                            <div className={style.transaction}>
                                <p>
                                    {transaction.slice(0, 6) + "..." + transaction.slice(transaction.length - 4, transaction.length)}
                                </p>
                            </div>

                        </div>
                    ))
                }
            </div>
        </div>
    )
}